import { Injectable } from '@angular/core';
import { AngularFirestore } from '@angular/fire/compat/firestore';

@Injectable({
  providedIn: 'root'
})
export class FirestoreService {
  constructor(private afs: AngularFirestore) { }
  
  setValue(documentName: string) {
    this.afs.collection('chatmessages').doc(documentName).set({ 'number': '' }).then(() => {})
  }
  
  WaitforFirebaseChange(documentName: string) {
    this.afs.collection('chatmessages').doc(documentName).valueChanges().subscribe((data: any) => {
      alert("running")
    })
  }
}
