import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { LoginService } from 'src/app/services/login.service';
import { AngularFirestore } from '@angular/fire/compat/firestore';

@Component({
  selector: 'app-body',
  templateUrl: './body.component.html',
  styleUrls: ['./body.component.css']
})
export class BodyComponent implements OnInit {

  data: any
  spamnumber: string = ""

  constructor(private http: HttpClient, private router: Router, private loginService: LoginService, private afs: AngularFirestore) { }


  ngOnInit(): void {
    this.getContacts()
  }

  addContact(name: string, number: number) {
    const token = "Token " + this.loginService.getToken()
    this.http.post("http://16.171.27.165:8000/addcontact", { name: name, number: number }, { headers: { 'Authorization': token } }).subscribe((data: any) => {
      this.getContacts()
    }, (error: any) => { }
    );
  }

  addSpam(number: number) {
    const token = "Token " + this.loginService.getToken()
    this.http.post("http://16.171.27.165:8000/spam", { number: number }, { headers: { 'Authorization': token } }).subscribe((data: any) => {
      console.log(data)
    }, (error: any) => {

      console.log(error)
     }
    );
  }

  getContacts() {
    const token = "Token " + this.loginService.getToken()
    this.http.get("http://16.171.27.165:8000/getcontactlist", { headers: { 'Authorization': token } }).subscribe((data: any) => {
      this.data = data
    });
  }

  onSubmit(myform: any) {
    if (myform.form.status == "VALID") {
      this.addContact(myform.form.value.name, myform.form.value.number)
    } else {
      console.log("Please fill the empty input")
    }
  }

  onSubmitSpam(myform: any) {
    if (myform.form.status == "VALID") {
      this.addSpam(myform.form.value.number)
      this.spamnumber = ""
    } else {
      console.log("Please fill the empty input")
    }
  }

  handleChildData(data: string) {
    this.getContacts()
  }
}

