import { Component } from '@angular/core';
import { HttpClient} from '@angular/common/http';
import { LoginService } from 'src/app/services/login.service';
import { Router } from '@angular/router';
import { FirestoreService } from 'src/app/services/firestore.service';


@Component({
  selector: 'app-log-in-component',
  templateUrl: './log-in-component.component.html',
  styleUrls: ['./log-in-component.component.css']
})
export class LogInComponentComponent {

  constructor(private http: HttpClient, private loginService: LoginService, private router: Router, private FirestoreService: FirestoreService) { }
  errorMessage: string = ''
  errorMessagePresent: boolean = false

  getLogin(userName: string, password: string) {
    this.http.post("http://16.171.27.165:8000/signin", { username: userName, password: password }).subscribe((data: any) => {
      this.loginService.setToken(data.token)
      this.FirestoreService.setValue(data.token)
      this.router.navigate([''])
    }, (error: any) => {
      this.errorMessage = error.error.error
      this.errorMessagePresent = true
    }
    );
  }  

  onSubmit(myform: any) {
    this.getLogin(myform.form.value.username, myform.form.value.password)
  }
}
