import { Component, Input, Output, EventEmitter } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { LoginService } from 'src/app/services/login.service';

@Component({
  selector: 'app-contactlist',
  templateUrl: './contactlist.component.html',
  styleUrls: ['./contactlist.component.css']
})
export class ContactlistComponent {
  @Input() contactName!: string
  @Input() contactNumber!: number
  @Input() notification!: boolean

  @Output() dataEvent = new EventEmitter<string>();

  sendDataToParent() {
    const dataToSend = 'Notfication Added to Contact';
    this.dataEvent.emit(dataToSend);
  }

  constructor(private http: HttpClient, private router: Router, private loginService: LoginService) { }
  
  addNotification(number: number) {
    const token = "Token " + this.loginService.getToken()
    this.http.post("http://16.171.27.165:8000/addnotification", { number: number }, {headers: { 'Authorization': token }}).subscribe((data: any) => {
      this.sendDataToParent()
    },  (error: any) => {});
  }

  onNotificationChange(phone_number: number) {
    this.addNotification(phone_number)
  }
}
