import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LogInComponentComponent } from './pages/log-in-component/log-in-component.component';
import { RegisterComponentComponent } from './pages/register-component/register-component.component';
import { HomeComponent } from './pages/home/home.component';
import { AuthguardService } from './services/authguard.service';

const routes: Routes = [
  { path: '', component: HomeComponent, canActivate: [AuthguardService] },
  { path: 'signin', component: LogInComponentComponent },
  { path: 'signup', component: RegisterComponentComponent },
  { path: '**', redirectTo: 'signin' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
