import { Component, OnDestroy } from '@angular/core';
import { Subscription, debounceTime } from 'rxjs';
import { LoginService } from 'src/app/services/login.service';
import { AngularFirestore } from '@angular/fire/compat/firestore';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnDestroy {

  private valueChangesSubscription: Subscription = new Subscription;
  spam_number = ""
  user = ""
  displayNotification = true

  ngAfterViewInit() {
    const token = this.loginService.getToken()
    if (token != null) {
      let firsttime = true
      this.valueChangesSubscription = this.afs.collection('chatmessages').doc(token).valueChanges().pipe(debounceTime(500)).subscribe((data: any) => {
        if (firsttime == true) {
          firsttime = false
          return
        }
        this.spam_number = data.spam_number
        this.user = data.user
        this.displayNotification = false
        setTimeout(() => {this.displayNotification = true}, 5000)
      })
    }
  }

  ngOnDestroy() {
    if (this.valueChangesSubscription) {
      this.valueChangesSubscription.unsubscribe();
    }
  }

  constructor(private loginService: LoginService, private afs: AngularFirestore) { }

}
