import { Component } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-register-component',
  templateUrl: './register-component.component.html',
  styleUrls: ['./register-component.component.css']
})
export class RegisterComponentComponent {

  constructor(private http: HttpClient) { }
  errorMessage: string = ''
  errorMessagePresent: boolean = false
  successMessagePresent: boolean = false

  register(userName: string, password: string, phonenumber: number, name: string) {
    this.http.post("http://16.171.27.165:8000/signup", { username: userName, password: password, phone_number: phonenumber, name: name }).subscribe((data: any) => {
      this.successMessagePresent = true
    }, (error: any) => {
      this.errorMessage = error.error.error
      this.errorMessagePresent = true
    }
    );
  }  

  onSubmit(myform: any) {
    this.register(myform.form.value.username, myform.form.value.password, myform.form.value.phonenumber, myform.form.value.name)
  }

}
